 
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}


header {
  background-color: #f8f8f8;
  padding: 10px 20px;
  border-bottom: 1px solid #ddd;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  color: #2ca866;
}

.search-bar {
  width: 40%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 20px;
}

.location {
  font-size: 14px;
  color: #888;
  margin-left: 20px;
}

.icons span {
  margin: 0 10px;
  cursor: pointer;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 10px 0;
  border-top: 1px solid #ddd;
  background-color: #fff;
}

.all-departments {
  background-color: #2ca866;
  color: #fff;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}


.hero {
  display: flex;
  align-items: center;
  padding: 50px;
  background-color: #e9f5f0;
  gap: 20px;
}

.promo-content {
  max-width: 50%;
}

.promo-badge {
  display: inline-block;
  background-color: #ffd700;
  color: #000;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
  margin-bottom: 10px;
}

h1 {
  font-size: 36px;
  color: #1b1b1b;
  margin-bottom: 15px;
}

p {
  color: #555;
  margin-bottom: 20px;
}

.shop-now {
  background-color: #1b1b1b;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.promo-image img {
  max-width: 100%;
  border-radius: 10px;
}


.featured-categories {
  padding: 2rem;
  text-align: center;
}

.featured-categories h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.categories-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
}

.category-card {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 1rem;
  text-align: center;
}

.category-card img {
  width: 80px;
  height: auto;
  margin-bottom: 0.5rem;
}

.category-card p {
  font-size: 1rem;
  color: #555;
}

 
.promo-cards {
  display: flex;
  gap: 1rem;
  padding: 2rem;
}

.promo-card {
  flex: 1;
  background-color: #f8f9fa;
  padding: 1.5rem;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
}

.promo-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.promo-card p {
  color: #555;
  margin-bottom: 1rem;
}

.promo-card .shop-now {
  background-color: #1b5e20;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  text-decoration: none;
  margin-bottom: 1rem;
}

.promo-card img {
  width: 100px;
  height: auto;
  position: absolute;
  bottom: 10px;
  right: 10px;
}



.popular-products {
  padding: 2rem;
  background-color: #f9f9f9;
}

.popular-products h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.product-card {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 1rem;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.product-card img {
  width: 100px;
  height: auto;
  margin-bottom: 0.5rem;
}

.badge {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #ff5252;
  color: white;
  padding: 0.2rem 0.5rem;
  font-size: 0.8rem;
  border-radius: 3px;
}

.product-info h3 {
  font-size: 1rem;
  margin: 0.5rem 0;
}

.category {
  font-size: 0.85rem;
  color: #777;
  margin-bottom: 0.5rem;
}

.price-section {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.discounted-price {
  font-size: 1.1rem;
  font-weight: bold;
  color: #1b5e20;
}

.original-price {
  font-size: 0.9rem;
  text-decoration: line-through;
  color: #a0a0a0;
}

.rating {
  font-size: 0.85rem;
  color: #ffb400;
  margin: 0.5rem 0;
}

.add-button {
  background-color: #4caf50;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

.add-button:hover {
  background-color: #388e3c;
}

.daily-best-sellers {
  display: flex;
  gap: 1rem;
  padding: 2rem;
  background-color: #f9f9f9;
}

.best-sellers-banner {
  background-color: #f1f8e9;
  border-radius: 10px;
  padding: 1.5rem;
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.best-sellers-banner h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.best-sellers-banner p {
  font-size: 1rem;
  color: #555;
  margin-bottom: 1rem;
}

.shop-now-button {
  background-color: #4caf50;
  color: white;
  padding: 0.7rem 1.5rem;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: 1rem;
}

.shop-now-button:hover {
  background-color: #388e3c;
}

.best-sellers-products {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1rem;
  width: 70%;
}

.product-card {
  width: 180px;
  height: 250px;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  box-sizing: border-box;
}

.product-card img {
  width: 80px;
  height: 80px;
  margin-bottom: 0.5rem;
}

.product-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.product-info h3 {
  font-size: 0.9rem;
  margin: 0.5rem 0;
  color: #333;
  font-weight: normal;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.price {
  font-size: 1.1rem;
  color: #1b5e20;
  font-weight: bold;
  margin-top: 0.5rem; 
  margin-bottom: 0.5rem;  
}

.add-button {
  background-color: #4caf50;
  color: white;
  padding: 0.4rem 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  width: 80%;
  margin-top: auto;  
}

.add-button:hover {
  background-color: #388e3c;
}

